import React from 'react';
import PropTypes from 'prop-types';
import styles from './VisualGrid.module.scss';
import theme from '../../themes/data';

const Paths = () => {
  const paths = [];
  for (let i = 0; i < 25; i += 1) {
    paths.push(<path key={`path-v-${i}`} d={`M ${i * 40},0 V 1000`} />);
  }
  for (let i = 0; i < 25; i += 1) {
    paths.push(<path key={`path-h-${i}`} d={`M 0,${i * 40} H 1000`} />);
  }
  return paths;
};

const Svg = ({ className }) => (
  <svg
    xmlns="http://www.w3.org/2000/svg"
    width="1000"
    height="1000"
    viewBox="0 0 1000 1000"
    version="1.1"
    className={className}
  >
    <style>
      {`
        path, rect {
          fill: none;
          stroke-width: 1px;
          stroke: ${theme.secondary};
        }
        `}
    </style>
    <g>
      <Paths />
      <rect
        width="1000"
        height="1000"
        x="0"
        y="0"
      />
    </g>
  </svg>
);

Svg.displayName = 'MKVisualGridSvg';

Svg.propTypes = {
  className: PropTypes.string.isRequired,
};

Svg.defaultProps = {
};

const VisualGrid = () => (
  <div className={styles.root}>
    <Svg className={[styles.svg, styles.svgBottom].join(' ')} />
  </div>
);

VisualGrid.displayName = 'MKVisualGrid';

VisualGrid.propTypes = {
};

VisualGrid.defaultProps = {
};

export default VisualGrid;
