/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from 'react';
import PropTypes from 'prop-types';
import { useStaticQuery, graphql } from 'gatsby';
import { ToastContainer } from 'react-toastify';

import 'typeface-orbitron';

import Header from './Header';
import Footer from './Footer';
import Link from '../Link';
import './Layout.scss';

import styles from './Layout.module.scss';
import VisualGrid from './VisualGrid';
import Access from './Access';
// import Episodes from './Episodes';

const Layout = ({ children, noHeader = false }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `);

  const [visible, setVisible] = React.useState(1);
  const handleMouseMove = () => !visible && setVisible(true);
  React.useEffect(() => {
    const timer = setTimeout(() => setVisible(false), 1500);
    window.addEventListener('mousemove', handleMouseMove, { passive: true });
    return () => {
      clearTimeout(timer);
      window.removeEventListener('mousemove', handleMouseMove, { passive: true });
    };
  });

  return (
    <Access>
      <div className={styles.rootOverlay} />

      <div
        className={[
          styles.innerlay,
          visible && styles.innerlayVisible,
        ].filter(Boolean).join(' ')}
      >
        <div
          className={[
            styles.overlay,
            visible && styles.overlayVisible,
          ].filter(Boolean).join(' ')}
        >
          {!noHeader && (
            <Header
              className={styles.haeder}
              siteTitle={data.site.siteMetadata.title}
            />
          )}

          <div className={styles.menu}>
            <nav>
              <ul className={styles.menuLinks}>
                <li className={styles.menuLink}>
                  <Link color="secondary" to="/episodes">
                    Episodes
                  </Link>
                </li>
              </ul>
            </nav>
          </div>

          <Footer />
        </div>
      </div>

      <main className={styles.main}>{children}</main>

      <div className={styles.rootUnderlay} />

      <VisualGrid />

      <ToastContainer />
    </Access>
  );
};

Layout.propTypes = {
  children: PropTypes.node.isRequired,
  noHeader: PropTypes.bool,
};

Layout.defaultProps = {
  noHeader: false,
};

Layout.displayName = 'MKLayout';

export default Layout;
